@import url(https://fonts.googleapis.com/css?family=Anton|Fredoka+One|Squada+One&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: 0;
}

html, body, #root{
  height: 100%;
}

body{
  background: #f5f5f5;
}

body, input, button{
  font-family: Arial, Helvetica, sans-serif;
}
body{
    font-family: 'Squada One', cursive;
    overflow-x: hidden;
}


nav {
    height: 100vh;
    background: #8BC34A;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

a {
    text-decoration: none;
}

.logo a img {
    -webkit-transition: all .4s;
    transition: all .4s;
    border-radius: 50%;
    -webkit-transform: translateY(-2em);
            transform: translateY(-2em);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
    position: relative;
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
	-webkit-animation: float 6s ease-in-out infinite;
	        animation: float 6s ease-in-out infinite;
}


.chartjs-render-monitor {
    width: 101vw !important;
    height: 100vh;
    overflow: hidden;
    position: absolute;
    left: -.59em;
    bottom: -0.55em;
    opacity: 0.45;
}

canvas {
    /* margin: -50px -5px; */
    width: 100vw;
}

/*
.logo a img:hover {
    transition: all .4s;
    transform:scale(1.2);
    -webkit-box-shadow: 0px 0px 10px 0px rgba(255,255,255,0.25);
    -moz-box-shadow: 0px 0px 10px 0px rgba(255,255,255,0.25);
    box-shadow: 0px 0px 10px 0px rgba(255,255,255,0.25);
}
*/

ul {
    list-style: none;
}

ul li {
    padding: .5em;
    font-size: 150%;
}

ul.menu li p, ul.social-media li a i {
    color: white;
    -webkit-transition: all .4s;
    transition: all .4s;
    z-index: 99;
    position: relative;
}


ul.social-media {
    display: inline-flex;
}

ul.social-media li a i:hover {
    color: rgb(27, 134, 72);
    -webkit-transition: all .4s;
    transition: all .4s;
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
}


.menu{
    font-family: 'Squada One', cursive;
}

ul.social-media li i, .menu {
    color: white;
}

ul.social-media {
    position: absolute;
    top: 0;
    right: 0;
    margin: 3%;
}

._career{
    font-family: 'Anton', sans-serif;
    text-transform: uppercase;
    font-weight: 200;
}

._ghost{
    font-size: 350%;
    -webkit-transform:rotate(-15deg);
            transform:rotate(-15deg);
    z-index: 0;
    position: relative;
    opacity: 0.3;
    pointer-events: none;
    margin-top: -2em;
}

._negative_rotate{
    opacity: 0.2;
    font-size: 270%;
    -webkit-transform:rotate(15deg);
            transform:rotate(15deg);
}

/* DEVELOPMENT ICONS FLOATING */

._float_icons {
    width: 50vw;
    height: 50vh;
    position: absolute;
    pointer-events: none;
}

._icon {
    width: 10%;
    position: absolute;
    z-index: 1;
    -webkit-animation: wobble 6s ease-in-out infinite;
            animation: wobble 6s ease-in-out infinite;
}

._mouse {
    left: 60%;
    top: 50%;
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
    -webkit-animation: wobble 4s ease-in-out infinite;
            animation: wobble 4s ease-in-out infinite;
    z-index:3;
}

._coding {
    width: 20%;
    left: 10%;
    top: 45%;
    -webkit-animation: wobble 9s ease-in-out infinite;
            animation: wobble 9s ease-in-out infinite;
}

._gamepad {
    top: -2em;
    width: 15%;
    left: 30%;
    -webkit-transform: rotate(-31deg);
            transform: rotate(-31deg);
    -webkit-animation: wobble 12s ease-in-out infinite;
            animation: wobble 12s ease-in-out infinite;
}

._pen {
    left: 75%;
    top: 25%;
    width: 9%;
    -webkit-transform: rotate(-55deg);
            transform: rotate(-55deg);
    -webkit-animation: wobble 10s ease-in-out infinite;
            animation: wobble 10s ease-in-out infinite;
}


._smartphone {
    left: 65%;
    top: 5%;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
}

/* Text about */


section.about {
    width: 100%;
    display: flex;
    padding: 8em 0 8em 0;
}

.about_image {
    position: relative;
    width: 50%;
    float: left;
    align-content: center;
    justify-content: center;
}

.about_img img {
    width: 80%;
    margin: auto;
    border-radius: 8em;
}

.about_text {
    width: 50%;
    float: left;
    align-content: center;
    justify-content: center;
    text-align: center;
    margin: auto 0;
}

.about_text p {
    line-height: 25px;
    margin-top: 18px;
}

/* Tecnologies */

section.tecnologies {
    width: 100%;
    display: flex;
    padding: 8em 0 8em 0;
}

.tecnologies_image {
    position: relative;
    width: 50%;
    float: left;
    align-content: center;
    justify-content: center;
}

.tecnologies_img img {
    width: 80%;
    margin: auto;
    border-radius: 8em;
}

.tecnologies_text {
    width: 50%;
    float: left;
    align-content: center;
    justify-content: center;
    text-align: center;
    margin: auto 0;
}

.tecnologies_text p {
    line-height: 25px;
    margin-top: 18px;
}

/* Footer */

footer {
    display: flex;
    justify-content: center;
}

footer li {
    display: inline-block;
    padding: 0.50em 0.50em;
}
    



/* MEDIA QUERIES */
@media screen and (min-width: 768px) and (max-width: 1024px) {
    ul.social-media {
        right: 5%;
    }    
    
    ._ghost {
        font-size: 250%!important;
    }

    ._negative_rotate {
        font-size: 250%;
    }
}


@media screen and (min-width: 211px) and (max-width: 768px) {
    ._ghost{
        position: absolute;
        right: calc(50% - 40vw);
        top: 50%;
    }

    ._float_icons {
        width: 100vw;
        height: 60vh;
    }

    ._ghost {
        font-size: 100%;
    }

    ._negative_rotate {
        font-size: 150%;
    }
    
    .about_image {
        width: 45%;
    }
    
    .tecnologies_text {
        margin: auto 7px;
    }
}

/* ANIMATES */

@-webkit-keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}


@-webkit-keyframes wobble {
    0% { -webkit-transform: translateX(0%); transform: translateX(0%); }
    15% { -webkit-transform: translateX(-25%) rotate(-5deg); transform: translateX(-25%) rotate(-5deg); }
    30% { -webkit-transform: translateX(20%) rotate(3deg); transform: translateX(20%) rotate(3deg); }
    45% { -webkit-transform: translateX(-15%) rotate(-3deg); transform: translateX(-15%) rotate(-3deg); }
    60% { -webkit-transform: translateX(10%) rotate(2deg); transform: translateX(10%) rotate(2deg); }
    75% { -webkit-transform: translateX(-5%) rotate(-1deg); transform: translateX(-5%) rotate(-1deg); }
    100% { -webkit-transform: translateX(0%); transform: translateX(0%); }
  }


@keyframes wobble {
    0% { -webkit-transform: translateX(0%); transform: translateX(0%); }
    15% { -webkit-transform: translateX(-25%) rotate(-5deg); transform: translateX(-25%) rotate(-5deg); }
    30% { -webkit-transform: translateX(20%) rotate(3deg); transform: translateX(20%) rotate(3deg); }
    45% { -webkit-transform: translateX(-15%) rotate(-3deg); transform: translateX(-15%) rotate(-3deg); }
    60% { -webkit-transform: translateX(10%) rotate(2deg); transform: translateX(10%) rotate(2deg); }
    75% { -webkit-transform: translateX(-5%) rotate(-1deg); transform: translateX(-5%) rotate(-1deg); }
    100% { -webkit-transform: translateX(0%); transform: translateX(0%); }
  }
  

